import { useEffect, useState } from "react";
import { Field, Form } from "react-final-form";
import { Link, useLocation, useNavigate } from "react-router-dom";
import { AlertDismissible, InputField } from "common/components";
import loginBanner from "../../assets/images/login-banner.png";
import AuthService from "../../services/auth.service";

const LoginContainer = (props) => {
  const urlParams = new URLSearchParams(window.location.search);
  const location = useLocation();
  console.log(`LoginContainer :: location: ${JSON.stringify(location)}`);
  console.log(`LoginContainer :: props: ${JSON.stringify(props)}`);
  console.log(`LoginContainer :: doctorData: ${location?.state?.doctorData}`);
  console.log(`LoginContainer :: history: ${location?.state?.history}`);
  // localStorage.removeItem("user");
  const required = `Required`;
  const [message, setMessage] = useState();
  const [isLoading, setIsLoading] = useState(false);
  const navigate = useNavigate();

  const [state, setState] = useState({
    ...location?.state,
    isLoggedIn: false,
  });

  useEffect(() => {
    document.body.classList.add("account-page");
    return () => {
      document.body.classList.remove("account-page");
    };
  }, []);

  const sleep = (ms) => new Promise((resolve) => setTimeout(resolve, ms));
  const validate = async (values) => {
    const errors = {};
    if (!values.email) {
      errors.email = required;
    }
    if (!values.password) {
      errors.password = required;
    }
    return errors;
  };

  const onSubmit = async (values) => {
    setIsLoading(true);
    // await sleep(1500);
    console.log(`onSubmit :: ${JSON.stringify(values, 0, 2)}`);

    const response = await AuthService.login(values.email, values.password);
    setMessage(!!response.message && response.message);
    setIsLoading(!!response.success && response.success);
    // setIsLoading(false);

    if (!response.success) return;

    if (response?.body?.status && response?.body?.status === "active") {
      if (response?.body?.accessToken) {
        localStorage.setItem("user", JSON.stringify(response?.body));
      }

      const history =
        location?.state?.history === "/treatments"
          ? "/treatments-summary"
          : location?.state?.history || "/en/account-profile";

      props.setIsLoggedIn(true);
      navigate(history, {
        state: {
          ...location?.state,
          isLoggedIn: true,
          prevPath: location.pathname,
        },
      });
    } else {
      setMessage([
        "Your account needs activation, please contact info@yourmedicalportal.com",
      ]);
    }
  };

  return (
    <div className="content">
      <div className="container-fluid">
        {!!location?.state?.doctorData ? (
          <div className="col-md-5 col-lg-4 theiaStickySidebar">
            {/* Booking Summary */}
            <div className="card booking-card">
              <div className="card-header">
                <h4 className="card-title">Booking Summary</h4>
              </div>
              <div className="card-body">
                <div className="booking-doc-info">
                  <img
                    src={location?.state?.doctorData?.image}
                    className="booking-doc-img"
                    alt="Doctor"
                  />
                  <div className="booking-info">
                    <h4>{location?.state?.doctorData?.doc_name}</h4>
                    <div className="clinic-details">
                      <p className="doc-location">
                        <i className="fas fa-map-marker-alt"></i>{" "}
                        {location?.state?.doctorData?.address}
                      </p>
                    </div>
                  </div>
                </div>
                <div className="booking-summary">
                  <div className="booking-item-wrap">
                    <ul className="booking-date p-2">
                      <li>
                        Approximate Date{" "}
                        <span>
                          {location?.state?.doctorData?.appointmentDate}
                        </span>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
            </div>
            {/* Booking Summary */}
          </div>
        ) : (
          ""
        )}
        <div className="row">
          <div className="col-md-8 offset-md-2">
            <div className="account-content">
              {location?.state?.preStatus &&
              location?.state?.preStatus === "success" ? (
                <AlertDismissible
                  type={location?.state?.preStatus}
                  boldMsg={`${location?.state?.loginBoldMsg}`}
                  msg={location?.state?.loginMsg}
                />
              ) : (
                ""
              )}
              <div className="row align-items-center justify-content-center">
                <div className="col-md-7 col-lg-6 login-left">
                  <img
                    src={loginBanner}
                    className="img-fluid"
                    alt="Your Medical Portal Login"
                  />
                </div>
                <div className="col-md-12 col-lg-6 login-right">
                  <div className="login-header">
                    <h3>Login to Your Medical Portal</h3>
                  </div>
                  <Form
                    validate={validate}
                    onSubmit={onSubmit}
                    // initialValues={{ doctorData }}
                    render={({
                      handleSubmit,
                      submitError,
                      form,
                      submitting,
                      pristine,
                      values,
                    }) => (
                      <form
                        onSubmit={handleSubmit}
                        className="needs-validation"
                        noValidate
                      >
                        <div className="invalid-feedback d-block mb-2">
                          {message}
                        </div>
                        <div
                          className={`form-group form-focus mt-4 ${
                            values?.email?.length > 0 ? "focused" : ""
                          }`}
                        >
                          <Field
                            name="email"
                            className="form-control floating"
                            label="Email"
                            type="text"
                            label_type="focus_label"
                            component={InputField}
                          />
                        </div>
                        <div
                          className={`form-group form-focus mt-4 ${
                            values?.password?.length > 0 ? "focused" : ""
                          }`}
                        >
                          <Field
                            name="password"
                            className="form-control floating"
                            label="Password"
                            type="password"
                            label_type="focus_label"
                            component={InputField}
                          />
                        </div>
                        <div className="text-right">
                          <Link to="/forgot-password" className="forgot-link">
                            Forgot Password ?
                          </Link>
                        </div>
                        <button
                          className="btn btn-primary btn-block btn-lg login-btn mt-5"
                          type="submit"
                          disabled={isLoading}
                        >
                          {!isLoading ? `Login` : ""}
                          {isLoading && (
                            <span className="spinner-border spinner-border-sm mb-1"></span>
                          )}
                        </button>
                        {/* Social Medica Login */}
                        {/* <div className="login-or">
                      <span className="or-line"></span>
                      <span className="span-or">or</span>
                    </div>
                    <div className="row form-row social-login">
                      <div className="col-6">
                        <a href="#0" className="btn btn-facebook btn-block">
                          <i className="fab fa-facebook-f mr-1"></i>Login
                        </a>
                      </div>
                      <div className="col-6">
                        <a href="#0" className="btn btn-google btn-block">
                          <i className="fab fa-google mr-1"></i>Login
                        </a>
                      </div>
                    </div> */}
                        {/* /Social Medica Login */}
                        <div className="text-center dont-have">
                          {`Donâ€™t have an account? `}
                          <Link to="/register">Register</Link>
                        </div>
                      </form>
                    )}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default LoginContainer;
